<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process150"></a>

<p><br></p>
<p><span class="rvts17">章节探索：失事洞穴</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:73,Y:107</span><span class="rvts21">，地点：失事洞穴</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">探索失事洞穴（支线：源力觉醒，浮木阴影，法律规则）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1098.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">失事洞穴位于浮木城的西边区域，靠近西海岸。出了城西门一直走，会遇到一处废旧的广场。驻守在广场里的有一些被虚空蛊惑控制的矮人和虚空飞虫。</span>
<br><span class="rvts21">失事洞穴只有这一处洞口，虽然山洞有另外一处海岸边的出口，但是那个出口位置是条死路。</span>
<br><span class="rvts21">该探索内容并非进入死亡海岸这个大章节时需要首先探索的地方，应具体参考所对应的支线任务（</span><span class="rvts20"></span><span class="rvts21">源力觉醒</span><span class="rvts20"></span><span class="rvts21">，</span><span class="rvts20"></span><span class="rvts21">浮木阴影</span><span class="rvts20"></span><span class="rvts21">，</span><span class="rvts20"></span><span class="rvts21">教会的法律</span><span class="rvts20"></span><span class="rvts21">）进行使用。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1099.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">由于背后的巨大阴谋，加上虚空虫子的肆意孵化，失事洞穴到处充满了危险。在这里可以找到</span><span class="rvts20"></span><span class="rvts21">浮木阴影</span><span class="rvts20"></span><span class="rvts21">，</span><span class="rvts20"></span><span class="rvts21">教会的法律</span><span class="rvts20"></span><span class="rvts21">，</span><span class="rvts20"></span><span class="rvts21">自由味道</span><span class="rvts20"></span><span class="rvts21">以及可以获得第二个源力点的</span><span class="rvts20"></span><span class="rvts21">源力觉醒</span><span class="rvts20"></span><span class="rvts21">支线任务的一些重要结点。</span>
<br><span class="rvts21">当进入洞穴后，发现尽头无路可走，折回头的时候却根据剧情需求，遭遇了几只凶猛的虚空虫子，一顿胖揍，和伙伴全部被分开。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1100.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">可以操作的主要角色被困在一处休息室。务必记住，主角是逃出该洞穴的关键人物。因为主角周围敌人较弱，比较容易逃脱。</span>
<br><span class="rvts21">但最终都要面对一只虚空飞虫的攻击，如果有幸不被它</span><span class="rvts20"></span><span class="rvts21">晕到死</span><span class="rvts20"></span><span class="rvts21">，尽可能使用近战击杀它。因为它的魔法抗性较高。</span>
<br><span class="rvts21">如果可以击杀飞虫，在离这里不远的地方就是神圣雕像，激活神圣雕像，可以使其它两名非战斗队友聚齐起来。</span>
<br><span class="rvts21">如果感觉无法击杀这只虫子，那么可以使用传送金字塔（在浮木城里面的两个矮人被困的地下室中可以找到），把队友传送过来，一起杀虫子。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1101.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">一名队友会困在一处像是炼金室的地方，这里面有很多好的装备可以找到，另外，可以找到钥匙并逃出该区域。不过还会遭遇另一只虚空飞虫。</span>
<br><span class="rvts21">如果主角已逃出，强烈建议让该队友在这里等待。（等待主角开启洞穴的传送点，然后传送到主角身边，避免一场恶战）。</span>
<br><span class="rvts21">另外，尽可能的搜索该区域，因为这里面有一些日记和信件是支线任务的关键物证。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1102.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">第二名队友比较悲催，他的位置位于虚空虫穴的中心，并且处于战斗状态。强烈建议不要与他们作战，先暂停在这里。使其它角色解困，再来这里帮助他。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1103.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">最后一名队友只有两只虚空虫子敌人，非常容易解决掉它们。但是和第一名队友一样，强烈建议不要使他探索该区域，而是战斗后原地不动。</span>
<br><span class="rvts21">等待主角解困后，和第一名队友一起传送到主角身边，解救第二位队友。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1104.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">其实如果主角无法击杀他要面对的虚空飞虫时，可以尝试使用物品</span><span class="rvts20"></span><span class="rvts21">传送金字塔</span><span class="rvts20"></span><span class="rvts21">，使另外两名非战斗队友回到自己身边。</span>
<br><span class="rvts21">如果主角解决了那只虫子，可以在不远处找到神像，使用神像可以把另外两名队友聚集起来。</span>
<br><span class="rvts21">这样以来，加上主角一共有</span><span class="rvts20">3</span><span class="rvts21">名队友可以聚集在一起了。接下来就是找到离雕像不远处的洞穴那里，解救第</span><span class="rvts20">4</span><span class="rvts21">名队友。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1105.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">找到最后被困在孵化室的队友，一起杀进虫穴中心。该洞穴最大的虚空虫威胁就会被彻底解决掉。</span>
<br><span class="rvts21">接下来就是探索洞穴中，中邪矮人的藏匿处。详情参考相应支线任务的章节。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



